@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;600;700&family=Montserrat:wght@300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

html{
  font-size: 62.5%; /* 1rem = 10px */
  box-sizing: border-box;
}
body{
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

*,
*::before,
*::after{
    box-sizing: inherit;
}


:root{
  --font-heading: 'Roboto', sans-serif;
  --font-homeHeading: 'Roboto Slab', serif;
  --font-para: 'Cabin', sans-serif;
  --font-numericTxt: 'Montserrat', sans-serif;

  --color-black: #000;
  --color-white: #fff;
  --color-grey1: #acacac;
  --color-grey2: #818181;
  --color-grey3: #666666;
  --color-green:#2E8473;
  --color-darkRed: #815063;
  --color-blue: #dbf3fa;
  
}

.main__heading{
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-heading);
  font-weight: 500;
  font-size: 5.4rem;
  letter-spacing: 1px;
}

.heading__text{
  font-family: 'Cabin', sans-serif;
  font-family: var(--font-para);
  font-weight: 400;
  font-size: 2rem;
  color: #818181;
  color: var(--color-grey2);
}

.sub__heading{
  font-family: 'Roboto', sans-serif;
  font-family: var(--font-heading);
  font-weight: 700;
  color: #000;
  color: var(--color-black);
  font-size: 3.8rem;
}

.paragraph{
  font-family: 'Cabin', sans-serif;
  font-family: var(--font-para);
  font-weight: 400;
  font-size: 1.8rem;
  color: #818181;
  color: var(--color-grey2);
  line-height: 1.6;
  letter-spacing: 1px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  text-align: justify!important;
}

a{
  text-decoration: none;
}

a:active{
  outline: none;

}

.Form__container{
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  display: flex;
  padding-top: 4.3rem;

}

.sideContainer__form{
  padding-top: 2.5rem;
  height: 100%;
  width: 50%;
  transition: all .5s ease;
  display: flex;
  justify-content: center;
  background-color: #FFFFFF;
  flex: 1 1;
  /* opacity: 0; */
}


.sideContainer__imgContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  flex:1 1;
  padding: 3rem 4rem ;
  opacity: 0;
}

.heading--formSideContainer{
  font-family: 'Montserrat', sans-serif;
  font-family: var(--font-numericTxt);
  color: #fff;
  color: var(--color-white);
  font-size: 4.2rem;
  font-weight: 500;
}

.sideImg{
  margin-top: 5rem;
  width: 62rem;
  object-fit: contain;
}



@keyframes fadeRight {
  0%{
      opacity: .2;
      transform: translateX(-50%);
  }
  100%{
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeLeft {
  0%{
      opacity: .2;
      transform: translateX(50%);
  }
  100%{
    opacity: 1;
    transform: translateX(0);
  }
}


@media only screen and (max-width:900px){
  html{
    font-size: 50%; /* 1rem = 8px */
  }

  .sideContainer__imgContainer{
    display: none;
  }

  
}



.spinner svg{
    margin-left: 50%;
    transform: translate(-50%);
}
.header{
    width: 100%;
    height: 6rem;
    background-color: #EAF3F1;
    padding: 2rem 8rem;
    display: flex;
    align-items: center;
    z-index: 1000; 
    position: fixed;
} 

.logo{
    width:3rem;
    height: 3rem;
    object-fit: cover;
    cursor: pointer;
}

.nav--header{
    margin-left: auto;
    width: 30%;
}

.burger__icon{
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all .2s ease;
    }
    
    .burger__icon div:not(:first-child){
      margin-top: 5px;
    }
    
    .burger__icon--line{
    border-radius: 1px;
    width: 25px;
    height: 3px;
    background-color: var(--color-green);
    transition: all .1s ease;
    }
    
    .burger__icon.opened{
      transform: translateY(2px);
    } 
    
    .burger__icon.opened  .burger__icon--line{
      margin-top: 0rem;
    }
    
    .burger__icon.opened  .burger__icon--line2{
      display: none;
    }
    
    .burger__icon.opened  .burger__icon--line1{
      transform:translate(1px,-1px) rotate(45deg );
    }
    
    .burger__icon.opened  .burger__icon--line3{
      transform:translateY(-4px) rotate(-45deg );
    }

@media only screen and (max-width: 1000px){
    .nav--header{
        width: 50%;
    }

}

@media only screen and (max-width: 600px){
    .header{
        padding: 2rem 4rem;
    }
    .nav--header{
        display: flex;
        justify-content: flex-end;
    }
}

.nav{
    display: flex;
    justify-content: space-around;
  }
  
  .navItem a{
    text-decoration: none;
    font-size: 2rem;
    color: #474747;
    font-family: var(--font-heading);
    font-weight: 800;
    letter-spacing: 1px;
    transition: all .1s ease-in-out;
  }

  .navItem a:visited, .navItem a:focus-visible{
      outline: none;
      border: none;
  }
  
 .navbar__link--active {
    color: var(--color-green)!important;
  }
  
  .navItem:hover a{
    color: var(--color-green);
  }




.footer{
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    background-color: #333;
    width: 100%;
}

.footer__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 6rem;
    padding-top: 4.5rem;
}

.footer__logo {
        width:5rem;
        height: 5rem;
        object-fit: cover;
}

.footer__info{
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 3rem;
}

.paragraph--footer{
    padding-top: 1.5rem;
}

.nav--footer a{
    color: var(--color-grey1);
}

.nav--footer a:hover{
    color: var(--color-grey1);
}

.nav--footer .navItem.active a{
    color: var(--color-grey1);
}

.nav--footer a:active{
    color: var(--color-grey1);
}

.nav--footer{
    padding-top: 1.5rem;
    border-top: 1px solid var(--color-grey1);
    width: 40%;
}

.footer--textBox{
    border-top: 1px solid var(--color-grey1);
    width: 40%;
}

.footer__iconBox{
    display: flex;
    justify-content: center;
    margin-top: 3rem;
}

@media only screen and (max-width:600px){
    .footer__info{
        flex-direction: column;
        align-items: center;
    }

    .nav--footer{
        width: 90%;
    }

    .footer--textBox{
        margin-top: 5rem;
        width: 90%;
    }

    .paragraph--footer{
        text-align: center;
    }

}
.mobileNav__container{
    margin-top: 6rem;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color:rgba(0,0,0,.9);
    z-index: 1000;
    transform: translateX(100%);
    transition: all .2s ease-in-out;
}

.mobileNav__itemsContainer{
    width: 50%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -60%);
}

.mobileNavItem{
    margin-top: 4rem;
}

.mobileNavItem a{
    display: inline-block;
    border: 1px solid var(--color-green);
    font-family: var(--font-numericTxt);
    font-size: 3rem;
    font-weight: 700;
    padding: 1rem 2rem;
    color: var(--color-white);
    text-decoration: none;
    text-transform: uppercase;
    background-image: linear-gradient(120deg, transparent 0%, transparent 50%, var(--color-white) 50%);
    background-size: 225%;
    transition: all .4s;
}

.mobileNavItem a:hover, 
.mobileNavItem a:active{
    background-position: 100%;
    color: var(--color-green);
    transform: translateX(1rem);
}
.showNav{
    transform: translateX(0%);
}
